import React, { Fragment, Component } from "react";
import Header from "header/header";
import Loading from "loading/lottie";
import ReturnTop from "returnTop/index";
import WaitLoad from "./waitLoad";
import { ListView } from "antd-mobile";
import Item from "../evaluateItem/item";
import styles from "./style.module.css";

const listCache = function({ options, method = "GET", type = "new" }) {
  return new Promise((resolve, reject) => {
    const { site } = options;
    const getActiveName = site(this.state.activeName);
    const getAjax = new XMLHttpRequest();
    getAjax.open(method, getActiveName);
    getAjax.send();
    getAjax.onload = () => {
      if (getAjax.readyState === 4 && getAjax.status === 200) {
        const comment = JSON.parse(getAjax.response).data;
        const result =
          type === "new"
            ? comment.result
            : this.state.result.concat(comment.result);
        this.setState({
          pagination: comment.pagination,
          result: comment.result,
          dataSource: this.state.dataSource.cloneWithRows(result)
        });
        return resolve(true);
      } else {
        return reject(new Error("服务器请求失败"));
      }
    };
  });
};
async function startLoading({ startOptions, nextOptions, method = "GET" }) {
  const firstCache = () =>
    new Promise((resolve, reject) => {
      const { site } = startOptions;
      const getAjax = new XMLHttpRequest();
      getAjax.open(method, site);
      getAjax.send();
      getAjax.onload = () => {
        if (getAjax.readyState === 4 && getAjax.status === 200) {
          const comment = JSON.parse(getAjax.response);
          this.setState({
            ...comment,
            Cache: true,
            activeName: comment.evaluateList[0].name
          });
          return resolve(true);
        } else {
          return reject(new Error("服务器请求失败"));
        }
      };
    });
  const nextCache = listCache.bind(this);
  await firstCache();
  await nextCache({ options: nextOptions });
}

class EvaluateList extends Component {
  constructor() {
    super(...arguments);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2
    });
    this.hasMore = this.hasMore.bind(this);
    this.onEndReached = this.onEndReached.bind(this);
    this.tagSelect = this.tagSelect.bind(this);
    this.state = {
      dataSource,
      Cache: false,
      beyond: false, //标签列表是否过多，过多则隐藏一部分
      haveMore: false //标签栏过多时，显示更多按钮
    };
  }
  componentDidMount() {
    const getData = startLoading.bind(this);
    getData({
      startOptions: { site: `/commentTags?id=${this.props.url}` },
      nextOptions: {
        site: activeName =>
          `/comment?itemId=${this.props.url}&tag=${activeName}&page=1`
      }
    });
  }
  onEndReached() {
    const { totalPage, page } = this.state.pagination;
    if (page + 1 > totalPage) {
      this.setState({
        result: false
      });
      return false;
    }
    const getState = listCache.bind(this);
    const options = {
      site: activeName =>
        `/comment?itemId=${this.props.url}&tag=${activeName}&page=${this.state
          .pagination.page + 1}`
    };
    getState({ options, type: "add" });
  }
  hasMore(listRef) {
    if (Object.is(listRef, null)) {
      return null;
    }
    this.setState({
      beyond: listRef.scrollHeight === listRef.clientHeight ? null : true
    });
  }
  async tagSelect(name) {
    const synchronousState = () =>
      new Promise(resolve => {
        this.setState(
          {
            activeName: name,
            dataSource: this.state.dataSource.cloneWithRows([])
          },
          resolve
        );
      });
    const getState = listCache.bind(this);
    const options = {
      site: activeName =>
        `/comment?itemId=${this.props.url}&tag=${activeName}&page=1`
    };
    await synchronousState();
    await getState({ options });
  }
  render() {
    if (!this.state.Cache) {
      return <Loading stop={this.state.Cache} />;
    }
    const { star, goodCmtRate, evaluateList, activeName } = this.state;

    return (
      <div>
        <ReturnTop />
        <Header />
        <div className={styles.wrapper}>
          <div className={styles.header}>
            <div className={styles.grade}>
              评分
              {Array.from({ length: star }).map((value, index) => (
                <Fragment key={index}>
                  <div className={styles.starIcon} />
                </Fragment>
              ))}
              <span className={styles.proportion}>{goodCmtRate}</span>
            </div>
            <div
              className={
                this.state.beyond && this.state.haveMore !== false
                  ? styles.release
                  : styles.tagList
              }
              ref={this.hasMore}
            >
              {evaluateList.map(({ name, strCount }, index) => (
                <Fragment key={index}>
                  <span
                    className={` ${styles.tag} ${
                      activeName === name ? styles.active : ""
                    }`}
                    onClick={() => this.tagSelect(name)}
                  >
                    {name}({strCount})
                  </span>
                </Fragment>
              ))}
            </div>
            {this.state.beyond ? (
              <div
                className={styles.more}
                onClick={() =>
                  this.setState({
                    haveMore: !this.state.haveMore
                  })
                }
              >
                {this.state.haveMore ? "收起" : "更多"}
              </div>
            ) : null}
          </div>
          <ListView
            dataSource={this.state.dataSource}
            renderRow={(rowData, sectionID, rowID) => (
              <Item {...rowData} id={sectionID + rowID} />
            )}
            onEndReached={this.onEndReached}
            scrollRenderAheadDistance={500}
            onEndReachedThreshold={500}
            useBodyScroll
          />
        </div>
        {this.state.result === false ? null : <WaitLoad />}
      </div>
    );
  }
}

export default EvaluateList;



